import { ActionButton } from '@popup/shared/components/ActionButton';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

type ExitPopUpProps = {
  onClose: () => void;
};
export function ExitPopUp({ onClose }: ExitPopUpProps): JSX.Element {
  const { clearPremiumData } = usePremiumData();

  return (
    <>
      <h3>Вы уверены что хотите выйти из аккаунта?</h3>
      <ActionButton onClick={clearPremiumData}>Да</ActionButton>
      <ActionButton onClick={onClose}>Нет</ActionButton>
    </>
  );
}
