import { LocalStorageKey } from '@app/storage/local/shared';
import { Button } from '@popup/shared/components/Button';
import { CopyId } from '@popup/shared/components/CopyId';
import { ExitButton } from '@popup/shared/components/ExitButton';
import { PopUpContainer } from '@popup/shared/components/PopUpContainer';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { clsx } from 'clsx';
import { useCallback, useState } from 'react';
import { createPortal } from 'react-dom';
import { Link } from 'react-router-dom';
import { RoutePath } from '../../../routes/types/RoutePath';
import { PremiumTag } from '../../shared/PremiumTag';
import { CancelPremiumPopUp } from './CancelPremiumPopUp';
import { getRussianDayEnding, getRussianHourEnding } from './utils';

import styles from './PremiumData.module.css';

export function PremiumData(): JSX.Element | null {
  const { premiumDaysLeft, userId, secondsLeft } = usePremiumData();
  const [isUnfreeUser] = useLocalStorage(LocalStorageKey.IsUnfreeUser);

  const paymentStatus = usePaymentStatus();

  const popUp = document.getElementById('popup');

  const [isCancelPremiumPopUpVisible, setIsCancelPremiumPopUpVisible] =
    useState(false);

  const handleOpenCancelPremiumPopUp = useCallback(() => {
    setIsCancelPremiumPopUpVisible(true);
  }, []);

  const handleCloseCancelPremiumPopUp = useCallback(() => {
    setIsCancelPremiumPopUpVisible(false);
  }, []);

  function renderTimeLeft(): JSX.Element | null {
    if (premiumDaysLeft === undefined) {
      return null;
    }

    if (premiumDaysLeft > 1 || !secondsLeft) {
      return (
        <div
          className={clsx({
            [styles['no-time-left']]: premiumDaysLeft <= 1,
          })}
        >
          {premiumDaysLeft % 10 === 1 ? 'Остался' : 'Осталось'}{' '}
          <span className={styles.time}>{premiumDaysLeft}</span>{' '}
          {getRussianDayEnding(premiumDaysLeft)}
        </div>
      );
    }

    const hours = Math.ceil(secondsLeft / 3600);
    return (
      <div
        className={clsx({
          [styles['no-time-left']]: hours <= 3,
        })}
      >
        {hours % 10 === 1 ? 'Остался' : 'Осталось'}{' '}
        <span className={styles.time}>{hours}</span>{' '}
        {getRussianHourEnding(hours)}
      </div>
    );
  }

  if (userId === undefined || premiumDaysLeft === undefined) {
    return null;
  }

  return (
    <>
      <div className={styles.wrapper}>
        <div className={styles['personal-data']}>
          <div className={styles['payment-container']}>
            <PremiumTag />
            <div className={styles['payment-data']}>
              <CopyId userId={userId} align="right" />
              <ExitButton />
            </div>
          </div>
          <div className={clsx(styles.days)} id="days-left">
            {renderTimeLeft()}
          </div>
        </div>
        <div className={styles.buttons}>
          <Link
            to={RoutePath.PremiumInfo}
            tabIndex={-1}
            className={styles.extend}
          >
            <Button variant="primary-n" size="small" className={styles.extend}>
              Продлить Premium
            </Button>
          </Link>
          {paymentStatus !== 'Trial' &&
            (premiumDaysLeft > 0 ? (
              <Link
                to={RoutePath.CancelPremium}
                tabIndex={-1}
                className={styles.extend}
              >
                <Button
                  variant="secondary"
                  size="small"
                  className={styles.extend}
                >
                  Вернуть деньги
                </Button>
              </Link>
            ) : isUnfreeUser ? null : (
              <Button
                variant="secondary"
                size="small"
                className={styles.extend}
                onClick={handleOpenCancelPremiumPopUp}
              >
                Перейти на бесплатную версию
              </Button>
            ))}
        </div>
      </div>
      {!!popUp &&
        isCancelPremiumPopUpVisible &&
        createPortal(
          <PopUpContainer onClose={handleCloseCancelPremiumPopUp}>
            <CancelPremiumPopUp onClose={handleCloseCancelPremiumPopUp} />
          </PopUpContainer>,
          popUp,
        )}
    </>
  );
}
