import { Button } from '@popup/shared/components/Button';
import { ClockIcon } from '@popup/shared/components/ClockIcon';
import { Error } from '@popup/shared/components/errors/Error';
import { PremiumPromo } from '../../PremiumPromo';
import { Footer } from '../../shared/Footer';
import { Loader } from '../../shared/Loader';

import { useTariffs } from '../hooks/useTariffs';
import { AllTariffs } from './AllTariffs';

import { useFreeTrial } from '@popup/project/youboost-neo/hooks/useFreeTrial';
import styles from './PremiumInfoContent.module.css';

export function PremiumInfoContent(): JSX.Element {
  const {
    isCheckingFreeTrial,
    canTryFreeTrial,
    isFreeTrialError,
    isFreeTrialLoading,
    handleClickTryPremium,
  } = useFreeTrial();

  const { isTariffError, isTariffsLoading, tariffs } = useTariffs();

  if (isFreeTrialError || isTariffError) {
    return <Error />;
  }

  if (isFreeTrialLoading) {
    return <Loader className={styles.loader}>Переход на Premium</Loader>;
  }

  if (isCheckingFreeTrial || isTariffsLoading) {
    return <Loader className={styles['check-loader']} />;
  }

  if (canTryFreeTrial) {
    return (
      <div className={styles.wrapper}>
        <PremiumPromo className={styles.promo}>
          <div className={styles['clock-wrapper']}>
            <ClockIcon />
            <div className={styles['clock-text']}>
              <span>24</span> <span>часа</span>
            </div>
          </div>
          <p className={styles.text}>
            Попробуйте Premium бесплатно на 24 ч. Далее от 90₽ / месяц
          </p>

          <Button
            variant="primary"
            className={styles.button}
            size="small"
            onClick={handleClickTryPremium}
          >
            Попробовать бесплатно
          </Button>
        </PremiumPromo>
        <Footer />
      </div>
    );
  }

  return <AllTariffs tariffs={tariffs} />;
}
