import { Feature, FeatureList } from './types/Feature';
import { getFeaturesData } from '@app/storage/session/featuresData';
import {
  LocalStorageKey,
  setLocalStorageData,
} from '@app/storage/local/shared';

type Store = {
  featuresList: FeatureList;
  loading: boolean;
  error: Error | null;
};

let listeners: Array<() => void> = [];

let store: Store = {
  featuresList: {} as FeatureList,
  loading: false,
  error: null,
};

const featureStore = {
  async setFeatureValues(features: Partial<FeatureList>) {
    try {
      store = {
        ...store,
        loading: true,
        error: null,
        featuresList: {
          ...store.featuresList,
          ...features,
        },
      };

      await setLocalStorageData({
        [LocalStorageKey.FeatureList]: store.featuresList,
      });

      store.loading = false;

      emitChange();
    } catch (error) {
      store = {
        ...store,
        loading: false,
        error: error as Error,
      };
      console.error(error);
    }
  },
  subscribe(listener: () => void) {
    listeners = [...listeners, listener];
    return () => {
      listeners = listeners.filter((l) => l !== listener);
    };
  },
  getSnapshot() {
    return store;
  },

  async init() {
    try {
      store = {
        ...store,
        loading: true,
      };

      const features = await getFeaturesData();

      store = {
        featuresList: {
          [Feature.SwitchYoutubeInternet]:
            features[Feature.SwitchYoutubeInternet] || false,
          [Feature.DevMeta]: features[Feature.DevMeta] || false,
          [Feature.Version]: features[Feature.Version] || '',
          [Feature.PACLite]: features[Feature.PACLite] || false,
          [Feature.CAPTCHA_CRACKER_DISABLED]:
            features[Feature.CAPTCHA_CRACKER_DISABLED] || false,
        },
        loading: false,
        error: null,
      };

      emitChange();
    } catch (error) {
      store = {
        ...store,
        loading: false,
        error: error as Error,
      };
      console.error(error);
    }
  },
};

function emitChange() {
  for (let listener of listeners) {
    listener();
  }
}

featureStore.init();

export { store, featureStore };
