import {
  getLocalStoragePremiumData,
  LocalStoragePremiumKey,
} from '@app/storage/local/premium';
import { LocalStorageKey } from '@app/storage/local/shared';
import { getLocalStorageData } from '@app/storage/local/shared';
import { useTheme } from '@popup/shared/contexts/Theme';
import { useEffect, useRef } from 'react';
import { OnboardingStep } from '../components/Onboarding/ui/constants';

export function useThemeChanges(): void {
  const { setTheme } = useTheme();
  const isInitialized = useRef(false);

  useEffect(() => {
    async function getInitialState(): Promise<void> {
      if (isInitialized.current) {
        return;
      }

      const { onboardingScreenStatus } = await getLocalStorageData(
        LocalStorageKey.OnboardingScreenStatus,
      );

      const { hasPremium } = await getLocalStoragePremiumData(
        LocalStoragePremiumKey.HasPremium,
      );

      isInitialized.current = true;

      if (
        onboardingScreenStatus &&
        onboardingScreenStatus !== OnboardingStep.Done
      ) {
        return;
      }

      setTheme(hasPremium ? 'premium' : 'default');
    }

    void getInitialState();
  }, [setTheme]);

  useEffect(() => {
    const listener = async (
      changes: { [key: string]: chrome.storage.StorageChange },
      areaName: chrome.storage.AreaName,
    ) => {
      if (areaName === 'local' && changes[LocalStoragePremiumKey.HasPremium]) {
        const { onboardingScreenStatus } = await getLocalStorageData(
          LocalStorageKey.OnboardingScreenStatus,
        );

        if (
          onboardingScreenStatus &&
          onboardingScreenStatus !== OnboardingStep.Done
        ) {
          return;
        }

        setTheme(
          changes[LocalStoragePremiumKey.HasPremium].newValue
            ? 'premium'
            : 'default',
        );
      }
    };

    chrome.storage.onChanged.addListener(listener);

    return () => {
      chrome.storage.onChanged.removeListener(listener);
    };
  }, [setTheme]);
}
