import { type FeatureListResponse } from '../popup/shared/features/types/Feature';
import { httpClient } from './httpClient';

export async function fetchFeaturesData(): Promise<FeatureListResponse> {
  const featuresApiUrl = process.env.FEATURE_LIST_URL;

  const data = await httpClient.get<FeatureListResponse>(featuresApiUrl).json();

  return data;
}
