import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { useEffect, useState } from 'react';
import {
  Discount,
  Problem,
  Sorry,
  type CancelPage,
} from '../components/CancelPremium';

export function CancelPremium(): JSX.Element | null {
  const [page, setPage] = useState<CancelPage | null>(null);

  const paymentStatus = usePaymentStatus();


  useEffect(() => {
    if (!paymentStatus) {
      return;
    }

    setPage(
      paymentStatus === 'Trial' || paymentStatus === 'Expired'
        ? 'sorry'
        : 'problem',
    );
  }, [paymentStatus]);

  usePremiumAnimation();

  if (!page) {
    return null;
  }

  return (
    <>
      <Problem
        page={page}
        onNext={() => {
          setPage('discount');
        }}
      />
      <Discount
        page={page}
        onNext={() => {
          setPage('sorry');
        }}
      />
      <Sorry page={page} />
    </>
  );
}
