import { Button } from '@popup/shared/components/Button';
import { clsx } from 'clsx';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useDebugButton } from '../../DebugButtons/hooks/useDebugButton';

import styles from './DebugButton.module.css';

export function DebugButton({
  className,
}: {
  className?: string;
}): JSX.Element {
  const { debugInfo, handleCopy, isCopied } = useDebugButton();
  return (
    <>
      <CopyToClipboard text={debugInfo} onCopy={handleCopy}>
        <Button variant="link" className={clsx(styles.link, className)}>
          <div className={clsx(styles.info, { [styles.copied]: isCopied })}>
            <span>Отладка</span>
          </div>
          <span
            className={clsx(styles['copy-text'], {
              [styles.copied]: isCopied,
            })}
          >
            Скопировано
          </span>
        </Button>
      </CopyToClipboard>
    </>
  );
}
