import { Fragment } from 'react';
import clsx from 'clsx';
import { AdvCards } from './types/AdvCards';
import styles from './AdvCard.module.css';

type AdvCardProps = {
  card: AdvCards;
};

export function AdvCard({ card }: AdvCardProps) {
  return (
    <div
      className={clsx(
        styles.card,
        card.size === 'big' && styles.cardBig,
        card.size === 'small' && styles.cardSmall,
      )}
      key={card.title}
    >
      <div className={styles.cardImage}>
        <card.image />
      </div>
      <div className={styles.cardTitle}>
        {card.title.split('\n').map((line, index) => (
          <Fragment key={line}>
            {line}
            {index !== card.title.split('\n').length - 1 && <br />}
          </Fragment>
        ))}
      </div>
    </div>
  );
}
