import { useEffect, useLayoutEffect, useState } from 'react';
import { type Theme } from '../ThemeContext';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
} from '@app/storage/local/premium';
import { LocalStorageKey } from '@app/storage/local/shared';
import { getLocalStorageData } from '@app/storage/local/shared';

export function useThemeContext(): {
  theme: Theme;
  setTheme: (theme: Theme) => void;
  isThemeLoading: boolean;
} {
  const [isLoading, setIsLoading] = useState(true);
  const [theme, setTheme] = useState<Theme>('default');

  useEffect(() => {
    async function getStatus(): Promise<void> {
      setIsLoading(true);
      const { hasPremium } = await getLocalStoragePremiumData(
        LocalStoragePremiumKey.HasPremium,
      );
      if (hasPremium) {
        setTheme('premium');
      } else {
        setTheme('default');
      }
      setIsLoading(false);
    }
    void getStatus();
  }, []);

  useEffect(() => {
    async function listener(changes: {
      hasPremium?: { newValue: boolean };
    }): Promise<void> {
      const { onboardingScreenStatus } = await getLocalStorageData(
        LocalStorageKey.OnboardingScreenStatus,
      );

      if (changes.hasPremium) {
        if (onboardingScreenStatus && onboardingScreenStatus !== 'done') {
          return;
        }

        setTheme(changes.hasPremium.newValue ? 'premium' : 'default');
      }
    }
    chrome.storage.local.onChanged.addListener(listener);
    return () => {
      chrome.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  useLayoutEffect(() => {
    document.body.classList.toggle('premium', theme === 'premium');
  }, [theme]);

  return {
    theme,
    setTheme,
    isThemeLoading: isLoading,
  };
}
