import { Actions } from '@app/actions/shared';
import {
  CreateUserExpiredUserMessage,
  CreateUserExpiredUserSuccessMessage,
} from '@app/messages/shared';
import {
  deleteLocalStoragePremiumData,
  LocalStoragePremiumKey,
  setLocalStoragePremiumData,
} from '@app/storage/local/premium';
import {
  LocalStorageKey,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { useCallback, useEffect, useState } from 'react';

export function useHandlePremiumWithoutTelegram() {
  const [loading, setLoading] = useState(false);
  const [isSuccess, setSuccess] = useState(false);

  const handleClick = useCallback(async () => {
    setLoading(true);
    await deleteLocalStoragePremiumData(LocalStoragePremiumKey.UserId);

    // TODO: remove this after backend is ready
    await setLocalStorageData({
      [LocalStorageKey.IsUnfreeUser]: true,
    });
    //

    chrome.runtime.sendMessage<CreateUserExpiredUserMessage>({
      action: Actions.CreateUserExpiredUser,
    });
  }, []);

  useEffect(() => {
    const listener = (message: CreateUserExpiredUserSuccessMessage) => {
      if (message.action === Actions.CreateUserExpiredUserSuccess) {
        setLoading(false);
        setSuccess(true);
      }
    };

    chrome.runtime.onMessage.addListener(listener);

    return () => {
      chrome.runtime.onMessage.removeListener(listener);
    };
  }, []);

  return { handleClick, loading, isSuccess };
}
