import { ConnectButton } from '@popup/shared/components/ConnectButton';
import { useTheme } from '@popup/shared/contexts/Theme';
import { useFeaturesList } from '@popup/shared/features/hooks/useFeaturesList';
import { useOnboardingScreen } from '@popup/shared/hooks/useOnboardingScreen';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { useThemeChanges } from '@popup/project/youboost-neo/hooks/useThemeChanges';

import { Feedback } from '../../../../../shared/components/Feedback';
import { Feature } from '../../../../../shared/features/types/Feature';

import { ModeSwitcher } from '../../../components/ModeSwitcher';
import { Onboarding } from '../../../components/Onboarding';
import { PremiumData } from '../../../components/PremiumData';
import { PremiumPromo } from '../../../components/PremiumPromo';
import { Footer } from '../../../components/shared/Footer';

import { useChangeDevice } from '../../../hooks/useChangeDevice';
import { useModeSwitcher } from '../../../hooks/useModeSwitcher';
import { useNavigateToPremiumInfo } from '../../../hooks/useNavigateToPremiumInfo';
import { useUnfreeUserPaymentScreen } from '../../../hooks/useUnfreeUserPaymentScreen';

import { RoutePath } from '../../types/RoutePath';

import styles from './Root.module.css';
import { OnboardingStep } from '@popup/project/youboost-neo/components/Onboarding/ui/constants';

export function Root(): JSX.Element | null {
  const { isThemeLoading } = useTheme();
  const { hasPremium } = usePremiumData();

  const { featuresList, loading } = useFeaturesList();
  const { onboardingStatus, isLoading } = useOnboardingScreen();

  useChangeDevice();
  useNavigateToPremiumInfo();
  usePremiumAnimation({ isFreeUser: !hasPremium });
  useUnfreeUserPaymentScreen();
  useThemeChanges();

  const { animateScope, isYoutubeMode, onModeChange } = useModeSwitcher();

  if (isThemeLoading || loading || isLoading) {
    return null;
  }

  if (onboardingStatus !== OnboardingStep.Done) {
    return <Onboarding onboardingStatus={onboardingStatus} />;
  }

  return (
    <>
      {featuresList[Feature.SwitchYoutubeInternet] && (
        <ModeSwitcher
          hasPremium={!!hasPremium}
          isYoutubeMode={isYoutubeMode}
          onModeChange={onModeChange}
        />
      )}

      {featuresList[Feature.SwitchYoutubeInternet] && (
        <ConnectButton
          className={styles['connect-button-container']}
          onExpiredRoute={RoutePath.PremiumInfo}
        />
      )}

      {hasPremium ? (
        <PremiumData />
      ) : (
        <PremiumPromo animateScope={animateScope} />
      )}
      <Footer />
      <Feedback />
    </>
  );
}
