import { memo, useCallback, useMemo } from 'react';
import { Tariff } from '../../../../../../../../../../api/premium/types/Tariff';
import { TariffData } from '../../../../../../../../../../api/premium/urls';
import clsx from 'clsx';
import styles from './TariffComponent.module.css';

type TariffProps = {
  tariff: TariffData;
  hasOneMonthDiscount: boolean;
  minMonthPrice: number;
  onCLickTariff: (tariff: TariffData) => void;
  selectedTariff?: TariffData;
};

function RawTariffComponent({
  tariff,
  hasOneMonthDiscount,
  selectedTariff,
  minMonthPrice,
  onCLickTariff,
}: TariffProps): JSX.Element {
  const isDiscountTariff = useMemo(
    () => tariff.tariff === Tariff.DefaultOneMonth && hasOneMonthDiscount,
    [hasOneMonthDiscount, tariff.tariff],
  );

  const handleClickTariff = useCallback(() => {
    onCLickTariff(tariff);
  }, [tariff, onCLickTariff]);

  return (
    <button
      key={tariff.period}
      onClick={handleClickTariff}
      className={clsx(styles.tariff, {
        [styles.selected]: selectedTariff?.period === tariff.period,
        [styles.featured]: tariff.monthPrice === minMonthPrice,
        [styles.discount]: isDiscountTariff,
      })}
      type="button"
    >
      <div className={clsx({ [styles['discount-price']]: isDiscountTariff })}>
        {isDiscountTariff && (
          <div className={styles['prev-price']}>{tariff.price}₽</div>
        )}
        {isDiscountTariff ? 99 : tariff.price} ₽ / {tariff.period}
      </div>
      {tariff.price > tariff.monthPrice && (
        <span
          className={styles['second-price']}
        >{`(${tariff.monthPrice}₽ / месяц)`}</span>
      )}
    </button>
  );
}

export const TariffComponent = memo(RawTariffComponent);
