import { useTheme } from '@popup/shared/contexts/Theme';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import React, { useLayoutEffect } from 'react';
import { PaymentScreenContent } from '../components/PaymentScreenContent';

/**
 * PaymentScreen component sets the theme to 'premium' when mounted and
 * reverts to 'default' if the user doesn't have a premium subscription upon unmounting.
 * It renders the PaymentScreenContent component.
 */

export const PaymentScreen: React.FC = () => {
  const { setTheme } = useTheme();
  const { hasPremium } = usePremiumData();

  useLayoutEffect(() => {
    setTheme('premium');
    return () => {
      if (!hasPremium) setTheme('default');
    };
  }, [setTheme, hasPremium]);

  return <PaymentScreenContent />;
};
