import {
  getLocalStoragePremiumData,
  LocalStoragePremiumKey,
} from '@app/storage/local/premium';
import { useEffect, useState } from 'react';

function isDateWithin24Hours(paidUntil?: string): boolean {
  if (!paidUntil) {
    return false;
  }

  const paidUntilDate = new Date(paidUntil);
  const now = new Date();
  const diffTime = paidUntilDate.getTime() - now.getTime();
  const diffHours = diffTime / (1000 * 60 * 60);
  return diffHours >= 0 && diffHours <= 24;
}

export function useTrialStatus(): boolean {
  const [isTrial, setIsTrial] = useState<boolean>(false);

  useEffect(() => {
    async function getPaidUntil(): Promise<void> {
      const { paidUntil } = await getLocalStoragePremiumData(
        LocalStoragePremiumKey.PaidUntil,
      );
      setIsTrial(isDateWithin24Hours(paidUntil));
    }

    getPaidUntil();

    function handleStorageChange(
      changes: Record<string, chrome.storage.StorageChange>,
    ) {
      if (changes[LocalStoragePremiumKey.PaidUntil]?.newValue) {
        setIsTrial(
          isDateWithin24Hours(
            changes[LocalStoragePremiumKey.PaidUntil].newValue,
          ),
        );
      }
    }

    chrome.storage.local.onChanged.addListener(handleStorageChange);
    return () => {
      chrome.storage.local.onChanged.removeListener(handleStorageChange);
    };
  }, []);

  return isTrial;
}
