import React, { memo, useCallback } from 'react';

import { motion } from 'motion/react';
import { paymentButtonContent } from '../constants/paymentButtonContent';
import styles from './PaymentButton.module.css';
import clsx from 'clsx';
import { ButtonIcon } from './ButtonIcon';
import { PaymentMethod } from '../../../../../../../../../../app/storage/session/shared';

interface PaymentButtonProps {
  method: PaymentMethod;
  onClick: (method: PaymentMethod) => void;
  isSelected?: boolean;
  disabled?: boolean;
}

const RawPaymentButton: React.FC<PaymentButtonProps> = ({
  method,
  disabled,
  isSelected,
  onClick,
}) => {
  const handleClick = useCallback(() => {
    onClick(method);
  }, [method, onClick]);

  return (
    <motion.button
      className={clsx(styles.button, {
        [styles.selected]: isSelected,
      })}
      disabled={disabled}
      whileTap={{ scale: 0.98 }}
      onClick={handleClick}
    >
      <ButtonIcon isSelected={isSelected} paymentMethod={method} />
      {paymentButtonContent[method].title && (
        <span
          className={clsx(styles.title, {
            [styles.selected]: isSelected,
          })}
        >
          {paymentButtonContent[method].title}
        </span>
      )}
    </motion.button>
  );
};

export const PaymentButton = memo(RawPaymentButton);
