import {
  fetchCheckTariffAvailability,
  setupTariff,
} from '@api/premium/fetchPremiumData';
import { Tariff } from '@api/premium/types/Tariff';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
  getSubscriptionInfo,
  setLocalStoragePremiumData,
} from '@app/storage/local/premium';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { getSessionMetaApiData } from '@app/storage/session/metaApiData';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { wait } from '@popup/shared/utils/wait';
import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

type FreeTrialHookValues = {
  isCheckingFreeTrial: boolean;
  canTryFreeTrial: boolean;
  handleClickTryPremium: () => Promise<void>;
  isFreeTrialLoading: boolean;
  isFreeTrialError: boolean;
};

export function useFreeTrial(): FreeTrialHookValues {
  const [isCheckingFreeTrial, setIsCheckingFreeTrial] = useState(true);
  const [canTryFreeTrial, setCanTryFreeTrial] = useState(true);
  const [isFreeTrialLoading, setIsFreeTrialLoading] = useState(false);
  const [isFreeTrialError, setIsFreeTrialError] = useState(false);

  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    async function checkFreeTrial(): Promise<void> {
      try {
        const { hasUsedTrial } = await getLocalStoragePremiumData(
          LocalStoragePremiumKey.HasUsedTrial,
        );

        const premiumInfoState = location.state as {
          isPayment?: boolean;
        } | null;
        const canUseFreeTrial = hasUsedTrial === undefined || !hasUsedTrial;

        if (Boolean(premiumInfoState?.isPayment) || !canUseFreeTrial) {
          setCanTryFreeTrial(false);
          return;
        }
        const [{ apiBaseUrl }, { deviceId, deviceIp }, { userId }] =
          await Promise.all([
            getSessionMetaApiData(),
            getLocalStorageData([
              LocalStorageKey.DeviceId,
              LocalStorageKey.DeviceIp,
            ]),
            getLocalStoragePremiumData(LocalStoragePremiumKey.UserId),
          ]);
        if (userId && deviceId) {
          let status = 'disallowed';

          const { status: newStatus } = await fetchCheckTariffAvailability(
            apiBaseUrl,
            {
              userId,
              deviceId,
              deviceIp,
              tariff: Tariff.FreeTrial,
            },
          );
          status = newStatus;

          await setLocalStorageData({
            [LocalStorageKey.TariffAvailabilityStatus]: newStatus,
          });

          setCanTryFreeTrial(status === 'allowed');
        }
      } catch (error) {
        console.error(error);
        setIsFreeTrialError(true);
      } finally {
        setIsCheckingFreeTrial(false);
      }
    }
    void checkFreeTrial();
  }, [location.state]);

  async function handleClickTryPremium(): Promise<void> {
    try {
      setIsFreeTrialLoading(true);
      const [{ apiBaseUrl }, { deviceId, deviceIp, currentState }, { userId }] =
        await Promise.all([
          getSessionMetaApiData(),
          getLocalStorageData([
            LocalStorageKey.DeviceId,
            LocalStorageKey.DeviceIp,
            LocalStorageKey.CurrentState,
          ]),
          getLocalStoragePremiumData(LocalStoragePremiumKey.UserId),
        ]);

      if (userId && deviceId && canTryFreeTrial) {
        await setupTariff(apiBaseUrl, {
          userId,
          deviceId,
          deviceIp,
          tariff: Tariff.FreeTrial,
        });
        await setLocalStoragePremiumData({
          [LocalStoragePremiumKey.HasUsedTrial]: true,
        });

        await setLocalStorageData({
          [LocalStorageKey.TariffAvailabilityStatus]: 'disallowed',
        });

        await getSubscriptionInfo();

        if (currentState === 'connected') {
          await wait(500);
        }

        navigate(RoutePath.Root);
      } else if (!userId) {
        await setLocalStorageData({ onboardingScreenStatus: 'step-two' });
        navigate(RoutePath.Root);
      }
    } catch (error) {
      console.error(error);
      setIsFreeTrialError(true);
    } finally {
      setIsFreeTrialLoading(false);
    }
  }

  return {
    isCheckingFreeTrial,
    canTryFreeTrial,
    handleClickTryPremium,
    isFreeTrialLoading,
    isFreeTrialError,
  };
}
