import { Button } from '@popup/shared/components/Button';
import { TelegramIcon } from '@popup/shared/components/TelegramPromo/ui/TelegramIcon';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import clsx from 'clsx';
import { forwardRef, useCallback } from 'react';
import Qr from '../../../assets/qr/qr.png';
import { StepProps } from '../../types';
import { CloseButton } from '../CloseButton/CloseButton';
import { Separator } from '../Separator/Separator';

import styles from './VerificationStep.module.css';

const TELEGRAM_GET_ID_LINK = process.env.TELEGRAM_GET_ID_LINK;

export const VerificationStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleContinue = useCallback(async () => {
      await onStepChange(4);
    }, [onStepChange]);

    const handleClose = useCallback(() => {
      onStepChange(0);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.title}>
            Получите доступ
            <CloseButton onClick={handleClose} fixed />
          </div>
          <div className={styles.description}>
            Чтобы использовать бесплатную версию ЮБуст NEO, пожалуйста,
            авторизуйтесь через Telegram-бота.
          </div>
          <div className={styles.qrSection}>
            <a
              href={TELEGRAM_GET_ID_LINK}
              onClick={handleContinue}
              target="_blank"
            >
              <img
                className={styles.qrImage}
                src={Qr}
                alt="QR Code"
                width={109}
                height={109}
              />
            </a>
            <div className={styles.qrLinks}>
              <div className={styles.qrLinkContainer}>
                <div className={styles.qrLinkTitle}>
                  Получите ID в Telegram-боте
                </div>
                <a
                  className={styles.qrLink}
                  href={TELEGRAM_GET_ID_LINK}
                  onClick={handleContinue}
                  target="_blank"
                >
                  @uboost_start_bot
                </a>
                <a
                  className={clsx(styles.button, styles.linkButton)}
                  href={TELEGRAM_GET_ID_LINK}
                  target="_blank"
                >
                  <Button
                    className={clsx(styles.getIdButton, styles.button)}
                    variant="secondary"
                    disabled={loading}
                    onClick={handleContinue}
                  >
                    <TelegramIcon />
                    Получить ID
                  </Button>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            disabled={loading}
            onClick={handleContinue}
          >
            Ввести ID
          </Button>
          <Separator />
          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleClick}
            disabled={loading}
            loading={loading}
          >
            Купить Premium — без авторизации
          </Button>
        </div>
      </div>
    );
  },
);

VerificationStep.displayName = 'VerificationStep';
