import { LocalStorageKey } from '@app/storage/local/shared';
import { useLocalStorage } from '../../../shared/hooks/useLocalStorage';
import { useNavigate } from 'react-router-dom';
import { useEffect, useRef } from 'react';
import { RoutePath } from '../routes/types/RoutePath';
import { Actions } from '@app/actions/shared';
import { CreateUserExpiredUserSuccessMessage } from '@app/messages/shared';

export function useUnfreeUserPaymentScreen() {
  const [isUnfreeUser] = useLocalStorage(LocalStorageKey.IsUnfreeUser);
  const [isNavigated, setIsNavigated] = useLocalStorage(
    LocalStorageKey.IsUnfreeUserPaymentScreenNavigated,
  );

  const navigate = useNavigate();

  useEffect(() => {
    const listener = async (message: CreateUserExpiredUserSuccessMessage) => {
      if (message.action === Actions.CreateUserExpiredUserSuccess) {
        if (isUnfreeUser && !isNavigated) {
          await setIsNavigated(true);
          navigate(RoutePath.PremiumInfo);
        }
      }
    };

    chrome.runtime.onMessage.addListener(listener);

    return () => {
      chrome.runtime.onMessage.removeListener(listener);
    };
  }, [isNavigated, isUnfreeUser, navigate, setIsNavigated]);
}
