import { Button } from '@popup/shared/components/Button';
import { StepProps } from '../../types';

import styles from './WelcomeStep.module.css';
import { forwardRef, useCallback } from 'react';
import Logo from '@popup/project/youboost-neo/components/Onboarding/assets/logo/neo.png';
import { Separator } from '../Separator/Separator';

export const WelcomeStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const handleContinue = useCallback(() => {
      onStepChange(1);
    }, [onStepChange]);

    const handleEnterId = useCallback(async () => {
      await onStepChange(4);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.logo}>
            <img src={Logo} alt="YouBoost NEO" />
          </div>
          <div className={styles.title}>
            Добро пожаловать
            <br />в ЮБуст NEO
          </div>
          <div className={styles.description}>
            ЮБуст NEO — это новая эпоха в развитии ЮБуст. Ускоряем{' '}
            <span className={styles.highlight}>YouTube</span> и открываем доступ
            ко всем <span className={styles.highlight}>недоступным сайтам</span>{' '}
            .
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleContinue}
          >
            Продолжить бесплатно
          </Button>
          <Separator />

          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleEnterId}
          >
            Я уже оплатил Premium
          </Button>
        </div>
      </div>
    );
  },
);

WelcomeStep.displayName = 'WelcomeStep';
