export function getRussianHourEnding(hour: number): string {
  let remainder = hour % 10;
  if (remainder === 1) return 'час';
  if (remainder >= 2 && remainder <= 4) return 'часа';
  return 'часов';
}

export function getRussianDayEnding(day: number): string {
  let remainder = day % 10;
  if (remainder === 1) return 'день';
  if (remainder >= 2 && remainder <= 4) return 'дня';
  return 'дней';
}
