import { CookieKey, removeCookieData } from '@app/storage/cookies/shared';
import {
  LocalStoragePremiumKey,
  deleteLocalStoragePremiumData,
  getLocalStoragePremiumData,
  type PaymentStatus,
} from '@app/storage/local/premium';
import {
  deleteLocalStorageData,
  LocalStorageKey,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { useCallback, useEffect, useState } from 'react';

type PremiumDataHookValues = {
  userId?: string;
  premiumDaysLeft?: number;
  secondsLeft?: number;
  hasPremium?: boolean;
  isError: boolean;
  isLoading: boolean;
  clearPremiumData: () => Promise<void>;
};

export function usePremiumData(): PremiumDataHookValues {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [premiumDaysLeft, setPremiumDaysLeft] = useState<number>();
  const [userId, setUserId] = useState<string>();
  const [hasPremium, setHasPremium] = useState<boolean>();
  const [isError, setIsError] = useState<boolean>(false);
  const [subStatus, setSubStatus] = useState<PaymentStatus | undefined>();
  const [premiumSecondsLeft, setPremiumSecondsLeft] = useState<
    number | undefined
  >();

  useEffect(() => {
    async function getPremiumInfo(): Promise<void> {
      setIsLoading(true);
      const {
        userId: id,
        daysLeft,
        hasPremium: hasPremiumCurrently,
        isSubscriptionInfoError,
        status,
        secondsLeft,
      } = await getLocalStoragePremiumData([
        LocalStoragePremiumKey.UserId,
        LocalStoragePremiumKey.DaysLeft,
        LocalStoragePremiumKey.HasPremium,
        LocalStoragePremiumKey.IsSubscriptionInfoError,
        LocalStoragePremiumKey.Status,
        LocalStoragePremiumKey.SecondsLeft,
      ]);
      setUserId(id);
      setPremiumDaysLeft(daysLeft);
      setHasPremium(Boolean(hasPremiumCurrently));
      setIsError(Boolean(isSubscriptionInfoError));
      setSubStatus(status);
      setPremiumSecondsLeft(secondsLeft);
      setIsLoading(false);
    }
    void getPremiumInfo();
  }, []);

  const clearAllAlarms = useCallback(async (): Promise<void> => {
    await chrome.alarms.clearAll();
  }, []);

  const clearLocalStoragePremiumData = useCallback(async (): Promise<void> => {
    await deleteLocalStoragePremiumData([
      LocalStoragePremiumKey.UserId,
      LocalStoragePremiumKey.DaysLeft,
      LocalStoragePremiumKey.HasPremium,
      LocalStoragePremiumKey.IsSubscriptionInfoError,
      LocalStoragePremiumKey.Status,
      LocalStoragePremiumKey.SecondsLeft,
      LocalStoragePremiumKey.IsChangeDeviceError,
      LocalStoragePremiumKey.IsSubscriptionInfoError,
      LocalStoragePremiumKey.PACPremiumData,
      LocalStoragePremiumKey.Status,
      LocalStoragePremiumKey.PaidUntil,
      LocalStoragePremiumKey.SecondsLeft,
      LocalStoragePremiumKey.IsAdBannerShown,
      LocalStoragePremiumKey.IsAllInternetProxy,
    ]);
  }, []);

  const clearLocalStorageData = useCallback(async (): Promise<void> => {
    await deleteLocalStorageData([
      LocalStorageKey.TariffAvailabilityStatus,
      LocalStorageKey.TargetState,
      LocalStorageKey.CurrentState,
      LocalStorageKey.IsUnfreeUser,
      LocalStorageKey.IsUnfreeUserPaymentScreenNavigated,
      LocalStorageKey.PACData,
      LocalStorageKey.PACRegistryData,
      LocalStorageKey.SavedTime,
      LocalStorageKey.IsLitePAC,
    ]);
  }, []);

  const clearCookies = useCallback(async (): Promise<void> => {
    await removeCookieData([CookieKey.UserId, CookieKey.DeviceId]);
  }, []);

  useEffect(() => {
    function listener(changes: {
      daysLeft?: { newValue?: number };
      secondsLeft?: { newValue?: number };
      hasPremium?: { newValue?: boolean };
      isSubscriptionInfoError?: { newValue?: boolean };
      status?: { newValue?: PaymentStatus };
    }): void {
      if (changes.daysLeft) {
        setPremiumDaysLeft(changes.daysLeft.newValue);
      }
      if (changes.secondsLeft) {
        setPremiumSecondsLeft(changes.secondsLeft.newValue);
      }
      if (changes.hasPremium) {
        setHasPremium(Boolean(changes.hasPremium.newValue));
      }
      if (changes.isSubscriptionInfoError) {
        setIsError(Boolean(changes.isSubscriptionInfoError.newValue));
      }
    }
    chrome.storage.onChanged.addListener(listener);
    return () => {
      chrome.storage.onChanged.removeListener(listener);
    };
  }, [subStatus]);

  useEffect(() => {
    function listener(changes: { userId?: { newValue?: string } }): void {
      if (changes.userId) {
        setUserId(changes.userId.newValue);
      }
    }
    chrome.storage.onChanged.addListener(listener);
    return () => {
      chrome.storage.onChanged.removeListener(listener);
    };
  }, []);

  const clearPremiumData = useCallback(async (): Promise<void> => {
    await clearLocalStoragePremiumData();
    await clearLocalStorageData();
    await clearCookies();
    await clearAllAlarms();

    await setLocalStorageData({ onboardingScreenStatus: 'step-zero' });

    setUserId(undefined);
    setPremiumDaysLeft(undefined);
    setHasPremium(undefined);
    setIsError(false);
    setPremiumSecondsLeft(undefined);
  }, [
    clearAllAlarms,
    clearCookies,
    clearLocalStorageData,
    clearLocalStoragePremiumData,
  ]);

  return {
    isLoading,
    hasPremium,
    userId,
    premiumDaysLeft,
    isError,
    secondsLeft: premiumSecondsLeft,
    clearPremiumData,
  };
}
