import { useEffect, useMemo, useState } from 'react';
import {
  getLocalStorageData,
  LocalStorageKey,
  OnboardingScreenStatus,
} from '@app/storage/local/shared';
import { OnboardingStep } from '@popup/project/youboost-neo/components/Onboarding/ui/constants';

type PremiumDataHookValues = {
  onboardingStatus: OnboardingScreenStatus;
  isLoading: boolean;
};

export function useOnboardingScreen(): PremiumDataHookValues {
  const [onboardingLoading, setOnboardingLoading] = useState<boolean>(true);
  const [cookieLoading, setCookieLoading] = useState<boolean>(true);
  const [onboardingStatus, setOnboardingStatus] =
    useState<OnboardingScreenStatus>(OnboardingStep.StepZero);

  useEffect(() => {
    async function getOnboardingData(): Promise<void> {
      setOnboardingLoading(true);
      const { onboardingScreenStatus } = await getLocalStorageData(
        LocalStorageKey.OnboardingScreenStatus,
      );

      setOnboardingStatus(onboardingScreenStatus || OnboardingStep.StepZero);
      setOnboardingLoading(false);
    }
    void getOnboardingData();
  }, []);

  useEffect(() => {
    function listener(changes: {
      onboardingScreenStatus?: { newValue?: OnboardingScreenStatus };
    }): void {
      if (changes.onboardingScreenStatus) {
        const newOnboardingStatus =
          changes.onboardingScreenStatus.newValue || OnboardingStep.StepZero;

        setOnboardingStatus(newOnboardingStatus);
      }
    }
    chrome.storage.onChanged.addListener(listener);
    return () => {
      chrome.storage.onChanged.removeListener(listener);
    };
  }, []);

  useEffect(() => {
    async function getCookieLoaded(): Promise<void> {
      const { cookieLoaded } = await getLocalStorageData(
        LocalStorageKey.CookieLoaded,
      );

      setCookieLoading(!cookieLoaded);
    }

    const listener = (changes: {
      cookieLoaded?: { newValue?: boolean };
    }): void => {
      if (changes.cookieLoaded) {
        const isCookieLoaded = !!changes.cookieLoaded.newValue;

        setCookieLoading(!isCookieLoaded);
      }
    };
    chrome.storage.local.onChanged.addListener(listener);

    void getCookieLoaded();

    return () => {
      chrome.storage.local.onChanged.removeListener(listener);
    };
  }, [onboardingStatus]);

  return useMemo(
    () => ({
      isLoading: onboardingLoading || cookieLoading,
      onboardingStatus,
    }),
    [onboardingLoading, cookieLoading, onboardingStatus],
  );
}
