import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { Button } from '@popup/shared/components/Button';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { clsx } from 'clsx';
import { type AnimationScope } from 'motion/react';
import { Link } from 'react-router-dom';

import { Loader } from '../../shared/Loader';
import { PremiumMiniHeader } from '../../shared/PremiumMiniHeader';

import styles from './PremiumPromo.module.css';
import { useFreeTrial } from '@popup/project/youboost-neo/hooks/useFreeTrial';

export type PremiumPromoProps = {
  animateScope?: AnimationScope<HTMLButtonElement>;
  children?: React.ReactNode;
  className?: string;
};

export function PremiumPromo({
  animateScope,
  children,
  className,
}: PremiumPromoProps): JSX.Element {
  const { userId } = usePremiumData();
  const { canTryFreeTrial, isCheckingFreeTrial } = useFreeTrial();

  if (isCheckingFreeTrial) {
    return <Loader />;
  }

  return (
    <>
      <div className={clsx(styles.wrapper, className)}>
        <PremiumMiniHeader userId={children ? undefined : userId} />
        <h3 className={styles.title}>
          4К-качество, отсутствие рекламы, открывает весь Интернет
        </h3>
        {children ? (
          children
        ) : (
          <Link to={RoutePath.PremiumInfo} className={styles.button}>
            <Button
              ref={animateScope}
              variant="primary"
              className={styles.button}
              size="small"
            >
              {canTryFreeTrial ? 'Попробовать бесплатно' : 'Подключить Premium'}
            </Button>
          </Link>
        )}
      </div>
    </>
  );
}
