import { type FetchTariffsResponse } from '@api/premium/urls';
import { useTheme } from '@popup/shared/contexts/Theme';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import {
  useCallback,
  useEffect,
  useLayoutEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useLocation } from 'react-router-dom';

type AllTariffsHookValues = {
  selectedTariff: FetchTariffsResponse[number] | undefined;
  minMonthPrice: number;
  handleClickTariff: (tariff: FetchTariffsResponse[number]) => void;
  hasOneMonthDiscount: boolean;
};

export function useAllTariffs(
  tariffs?: FetchTariffsResponse,
): AllTariffsHookValues {
  const [selectedTariff, setSelectedTariff] =
    useState<FetchTariffsResponse[number]>();

  const { setTheme } = useTheme();
  const { hasPremium } = usePremiumData();

  const isInitialTariffSelected = useRef(false);

  usePremiumAnimation();

  const location = useLocation();

  const hasOneMonthDiscount = useMemo(() => {
    const locationState = location.state as
      | {
          hasOneMonthDiscount: boolean;
        }
      | undefined;
    return Boolean(locationState?.hasOneMonthDiscount);
  }, [location?.state]);

  const minMonthPrice = useMemo(
    () =>
      tariffs?.length
        ? Math.min(...tariffs.map((tariff) => tariff.monthPrice))
        : 0,
    [tariffs],
  );

  useEffect(() => {
    if (!tariffs || isInitialTariffSelected.current) {
      return;
    }

    for (const tariff of tariffs) {
      if (tariff.monthPrice === minMonthPrice) {
        setSelectedTariff(tariff);
        isInitialTariffSelected.current = true;
      }
    }
  }, [minMonthPrice, tariffs]);

  const handleClickTariff = useCallback(
    (tariff: FetchTariffsResponse[number]) => {
      setSelectedTariff(tariff);
    },
    [],
  );

  useLayoutEffect(() => {
    setTheme('premium');
    return () => {
      if (!hasPremium) {
        setTheme('default');
      }
    };
  }, [setTheme, hasPremium]);

  return {
    selectedTariff,
    minMonthPrice,
    handleClickTariff,
    hasOneMonthDiscount,
  };
}
