type SubscriptionInfoHTTPError = {
  errors: [
    {
      code: 'change_device';
    },
    {
      code: 'user_required';
    },
    {
      code: 'device_id_required';
    },
    {
      code: 'user_not_found';
    },
  ];
};

type SubscriptionInfoError = {
  error:
    | 'changeDevice'
    | 'userRequired'
    | 'deviceIdRequired'
    | 'userNotFound'
    | 'internal'
    | 'unknown'
    | 'requestBlockedError';
};

export async function fetchSubscriptionInfo(
  apiBaseUrl: string,
  {
    userId,
    deviceId,
    deviceIp,
  }: {
    userId?: string;
    deviceId?: string;
    deviceIp?: string;
  },
): Promise<GetSubscriptionResponse | SubscriptionInfoError> {
  try {
    const apiUrl = getSubscriptionApiUrl(apiBaseUrl);
    const data = await httpClient
      .post<GetSubscriptionResponse>(apiUrl, {
        json: {
          userId,
          deviceId,
          deviceIp,
        },
      })
      .json();

    return data;
  } catch (error) {
    console.error(error);
    if (error instanceof HTTPError) {
      if (error.response.status === 400) {
        const errorData =
          await error.response.json<SubscriptionInfoHTTPError>();
        if (errorData.errors.find((err) => err.code === 'change_device')) {
          return { error: 'changeDevice' };
        }
        if (errorData.errors.find((err) => err.code === 'user_required')) {
          return { error: 'userRequired' };
        }
        if (errorData.errors.find((err) => err.code === 'device_id_required')) {
          return { error: 'deviceIdRequired' };
        }
        if (errorData.errors.find((err) => err.code === 'user_not_found')) {
          return { error: 'userNotFound' };
        }
      }
      if (error.response.status === 500) {
        return { error: 'internal' };
      }
    }
    if (error instanceof Error && error.message === 'Failed to fetch') {
      return { error: 'requestBlockedError' };
    }
    return { error: 'unknown' };
  }
}
