import { httpClient } from '@api/httpClient';
import { getChangeDeviceApiUrl } from '@api/premium/urls';
import {
  getLocalStorageData,
  LocalStorageKey,
} from '@app/storage/local/shared';
import { getSessionMetaApiData } from '@app/storage/session/metaApiData';

export async function changeDevice(userId: string): Promise<void> {
  const { apiBaseUrl } = await getSessionMetaApiData();
  const { deviceId, deviceIp } = await getLocalStorageData([
    LocalStorageKey.DeviceId,
    LocalStorageKey.DeviceIp,
  ]);
  const changeDeviceApiUrl = getChangeDeviceApiUrl(apiBaseUrl);
  await httpClient
    .post(changeDeviceApiUrl, {
      json: {
        userId,
        deviceId,
        deviceIp,
      },
    })
    .json();
}
