import { clsx } from 'clsx';
import { TelegramIcon } from './TelegramIcon';

import styles from './TelegramPromo.module.css';

type TelegramPromoProps = {
  className?: string;
};

export function TelegramPromo({ className }: TelegramPromoProps): JSX.Element {
  return (
    <a
      href={`https://t.me/${process.env.TELEGRAM_URL}`}
      target="_blank"
      className={className}
    >
      <button className={clsx(styles.container, styles.button, styles.size)}>
        <TelegramIcon className={styles.telegram} />
        <span>Telegram-канал</span>
      </button>
    </a>
  );
}
