import { Button } from '@popup/shared/components/Button';
import { useEnterIdForm } from '@popup/shared/components/EnterIdForm/hooks/useEnterIdForm';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import clsx from 'clsx';
import { forwardRef, useCallback, useEffect } from 'react';
import { Separator } from '../Separator/Separator';
import { BackButton } from './BackButton/BackButton';

import styles from './EnterIdStep.module.css';
import { setLocalStorageData } from '@app/storage/local/shared';
import { OnboardingStep } from '../../constants';

const TELEGRAM_GET_ID_LINK = process.env.TELEGRAM_GET_ID_LINK;

type EnterIdStepProps = {
  onStepChange: (step: number) => void;
};

export const EnterIdStep = forwardRef<HTMLDivElement, EnterIdStepProps>(
  ({ onStepChange }, ref) => {
    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const {
      enteredId,
      handleChange,
      handleSubmit,
      inputRef,
      isError,
      isLoading,
      clearError,
      isIdSettled,
    } = useEnterIdForm();

    const paymentStatus = usePaymentStatus();

    const handleBackButtonClick = useCallback(() => {
      onStepChange(3);
    }, [onStepChange]);

    useEffect(() => {
      async function updateOnboardingScreenStatus() {
        await setLocalStorageData({
          onboardingScreenStatus:
            paymentStatus !== 'New' && paymentStatus !== 'Expired'
              ? OnboardingStep.StepFour
              : OnboardingStep.Done,
        });
      }

      if (isIdSettled && paymentStatus) {
        updateOnboardingScreenStatus();
      }
    }, [isIdSettled, paymentStatus]);

    return (
      <div ref={ref}>
        <form onSubmit={handleSubmit} className={styles.form}>
          <div className={styles.content}>
            <BackButton onClick={handleBackButtonClick} />
            <h1 className={styles.title}>Введите полученный ID</h1>
            <p className={styles.description}>
              Введите оплаченный ID или получите
              <br />
              бесплатный в Telegram-боте:
              <br />
              <a
                className={styles.link}
                href={TELEGRAM_GET_ID_LINK}
                target="_blank"
              >
                @uboost_start_bot
              </a>
            </p>
            <div className={styles['input-container']}>
              <input
                ref={inputRef}
                className={clsx(styles.input, {
                  [styles['input-error']]: isError,
                })}
                onChange={handleChange}
                value={enteredId}
                placeholder="Введите ваш ID"
                onFocus={clearError}
                disabled={loading}
              />
              {isError && (
                <span className={styles.error}>
                  Кажется, вы ввели неверный ID. Попробуйте еще раз.
                </span>
              )}
            </div>
          </div>
          <div className={styles.buttons}>
            <Button
              className={styles.submit}
              type="submit"
              disabled={loading}
              loading={isLoading}
              loaderVariant="circle"
              loaderClassName={styles.loader}
            >
              {isLoading ? 'Проверяем ваш ID...' : 'Продолжить'}
            </Button>
            <Separator />
            <Button
              className={styles.button}
              variant="secondary"
              onClick={handleClick}
              disabled={loading}
              loading={loading}
            >
              Купить Premium — без авторизации
            </Button>
          </div>
        </form>
      </div>
    );
  },
);

EnterIdStep.displayName = 'EnterIdStep';
