import { clsx } from 'clsx';

import styles from './Error.module.css';

type ErrorProps = {
  className?: string;
  code?: number;
  isLoading?: boolean;
  text?: string | null;
  textLink?: React.ReactNode;
  buttonText?: string;
  onClick?: () => void;
  header?: React.ReactNode;
  footer?: React.ReactNode;
};

const defaultText =
  'Попробуйте перезапустить или полностью переустановить расширение. Если это не поможет, то рекомендуем подождать 30-60 минут и попробовать снова.';

const defaultButtonText = 'Перезапустить расширение';

export function Error({
  className,
  code,
  isLoading = false,
  text = defaultText,
  textLink,
  buttonText = defaultButtonText,
  onClick,
  header,
  footer,
}: ErrorProps): JSX.Element {
  const handleClick = onClick ?? (() => chrome.runtime.reload());

  return (
    <div className={clsx(styles.wrapper, className)}>
      {header && header}
      <div className={styles.error}>
        <div className={styles.emoji}>😢</div>
        <div>
          <h1>Упс... Возникла проблема</h1>
          <p>
            {text}
            {textLink && textLink}
          </p>
        </div>
        <button
          disabled={isLoading}
          type="button"
          className={styles.reload}
          onClick={handleClick}
        >
          <div className={styles['reload-content']}>
            {isLoading && <div className={styles.loader} />}
            {buttonText}
          </div>
        </button>
        {!!code && <span className={styles.code}>Код ошибки: {code} </span>}
      </div>
      {footer && footer}
    </div>
  );
}
