import { detect } from 'detect-browser';
import { useEffect, useRef, useState } from 'react';

type DebugButtonHookValues = {
  debugInfo: string;
  isCopied: boolean;
  handleCopy: () => void;
};

// TODO remove this function after the backend logic will be implemented
function reducePacScript(
  storageData: Record<string, unknown>,
): Record<string, unknown> {
  const { pacPremiumData, ...rest } = storageData;

  return {
    ...rest,
    pacPremiumData: pacPremiumData
      ? {
          updatedAt: (pacPremiumData as Record<string, unknown>)?.updatedAt,
        }
      : undefined,
  };
}

export function useDebugButton(): DebugButtonHookValues {
  const [debugInfo, setDebugInfo] = useState('');
  const [isCopied, setIsCopied] = useState(false);
  const timeoutRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    async function collectDebugInfo(): Promise<void> {
      try {
        const browserInfo = detect();
        const extensionInfo = {
          version: chrome.runtime.getManifest().version,
        };
        const platformInfo = await chrome.runtime.getPlatformInfo();
        const storageData = {
          local: reducePacScript(await chrome.storage.local.get()),
          session: await chrome.storage.session.get(),
        };
        const debugData = JSON.stringify(
          {
            browserInfo,
            extensionInfo,
            platformInfo,
            storageData,
            chromeRuntimeLastError: chrome.runtime.lastError?.message,
          },
          null,
          2,
        );
        const encodedDebugData = btoa(encodeURIComponent(debugData));
        setDebugInfo(encodedDebugData);
      } catch (error) {
        console.error('Error gathering debug information:', error);
      }
    }
    void collectDebugInfo();
  }, []);

  useEffect(() => {
    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    };
  }, []);

  function handleCopy(): void {
    setIsCopied(true);
    timeoutRef.current = setTimeout(() => {
      setIsCopied(false);
    }, 2000);
  }

  return {
    debugInfo,
    isCopied,
    handleCopy,
  };
}
