import { ConnectionState } from '@app/storage/local/shared';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { clsx } from 'clsx';
import { useConnectButton } from '../hooks/useConnectButton';
import { PauseIcon } from './PauseIcon';
import { PlayIcon } from './PlayIcon';
import { ErrorIcon } from './ErrorIcon';

import styles from './ConnectButton.module.css';

type StatusRus = 'ПОДКЛЮЧЕНО' | 'ПОДКЛЮЧИТЬСЯ' | 'ПОДКЛЮЧЕНИЕ' | 'ОШИБКА';

const STATUS_MAP: Record<ConnectionState, StatusRus> = {
  connected: 'ПОДКЛЮЧЕНО',
  disconnected: 'ПОДКЛЮЧИТЬСЯ',
  connecting: 'ПОДКЛЮЧЕНИЕ',
  error: 'ОШИБКА',
};

export const ConnectButton = ({
  onExpiredRoute,
  className,
}: {
  onExpiredRoute: string;
  className?: string;
}): JSX.Element => {
  const { handleClick, status } = useConnectButton({
    onExpiredRoute,
  });
  const paymentStatus = usePaymentStatus();
  const { hasPremium } = usePremiumData();

  const Icon = () => {
    if (status === 'connected') {
      return PauseIcon();
    }
    if (status === 'error') {
      return ErrorIcon();
    }
    return PlayIcon();
  };

  return (
    <div className={className}>
      <button
        className={clsx(
          styles['connect-button'],
          status === 'connected' && styles.enabled,
          status === 'connecting' && styles.connecting,
          status === 'error' && styles.error,
          paymentStatus === 'Expired' && hasPremium && styles.disabled,
        )}
        type="button"
        onClick={handleClick}
      >
        <div className={styles.content}>
          {status === 'error' ? (
            <div className={styles['error-info']}>
              <span>{STATUS_MAP[status]}</span>
              <br></br>
              <span className={styles['description']}>
                Попробуйте через 5 минут
              </span>
            </div>
          ) : (
            <span>{STATUS_MAP[status]}</span>
          )}
          <div className={styles.icon}>{Icon()}</div>
        </div>
      </button>
    </div>
  );
};

ConnectButton.displayName = 'ConnectButton';
