import { LocalStoragePremiumKey } from '@app/storage/local/premium';
import {
  getLocalStoragePremiumData,
  type ExtendedPaymentStatus,
} from '@app/storage/local/premium';
import { useEffect, useState } from 'react';
import { useTrialStatus } from './useTrialStatus';

export function usePaymentStatus(): ExtendedPaymentStatus | undefined {
  const [paymentStatus, setPaymentStatus] = useState<
    ExtendedPaymentStatus | undefined
  >();
  const [tempStatus, setTempStatus] = useState<
    ExtendedPaymentStatus | undefined
  >();

  const isTrial = useTrialStatus();

  useEffect(() => {
    async function fetchTempStatus(): Promise<void> {
      const { status } = await getLocalStoragePremiumData(
        LocalStoragePremiumKey.Status,
      );
      setTempStatus(status);
    }

    fetchTempStatus();

    function handleStorageChange(
      changes: Record<string, chrome.storage.StorageChange>,
    ) {
      if (changes[LocalStoragePremiumKey.Status]?.newValue) {
        setTempStatus(
          changes[LocalStoragePremiumKey.Status]
            .newValue as ExtendedPaymentStatus,
        );
      }
    }

    chrome.storage.local.onChanged.addListener(handleStorageChange);
    return () => {
      chrome.storage.local.onChanged.removeListener(handleStorageChange);
    };
  }, []);

  useEffect(() => {
    async function getPaymentStatus(): Promise<void> {
      setPaymentStatus(isTrial ? 'Trial' : tempStatus);
    }

    getPaymentStatus();
  }, [isTrial, tempStatus]);

  return paymentStatus;
}
